import { PayloadAction, createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { clientApi } from "../../api/api";
import { DrawTriangleList } from "../DrawDotListState";
import { IDrawTriangleItem, LinesListApi, PutLineApi } from "../type";

export interface SvgDrawState {
  lineList: IDrawTriangleItem[];
  currentLineItemID: number | null;
  isAdd: boolean;
  isEdit: boolean;
  progressLine: "success" | "loading" | "error";
}

const initialState: SvgDrawState = {
  lineList: DrawTriangleList,
  currentLineItemID: null,
  isAdd: false,
  isEdit: false,
  progressLine: "success"
};

export const PutLineToBase = createAsyncThunk<IDrawTriangleItem, IDrawTriangleItem>(
  "draw/changeLineBase",
  async (line, thunkApi) => {
    try {
      const response = await clientApi().put<PutLineApi>(
        "/draw/line",
        {
          id: line.id,
          x1: line.list[0].Coordinate.x,
          y1: line.list[0].Coordinate.y,
          x2: line.list[1].Coordinate.x,
          y2: line.list[1].Coordinate.y,
          color: `rgba(${line.color.r}, ${line.color.g}, ${line.color.b}, ${line.color.a})`
        }
      );
      const figure: IDrawTriangleItem = {
        id: response.data.id,
        color: line.color,
        type: "line",
        list: [
          {id: 1, clickDown: false, isMove: false, Coordinate: {x: response.data.x1, y: response.data.y1}},
          {id: 2, clickDown: false, isMove: false, Coordinate: {x: response.data.x2, y: response.data.y2}}
        ]
      }

      return figure;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const PostLineToBase = createAsyncThunk<IDrawTriangleItem, IDrawTriangleItem>(
  "draw/addLineBase",
  async (line, thunkApi) => {
    try {
      const response = await clientApi().post<PutLineApi>(
        "/draw/line",
        {
          x1: line.list[0].Coordinate.x,
          y1: line.list[0].Coordinate.y,
          x2: line.list[1].Coordinate.x,
          y2: line.list[1].Coordinate.y,
          color: `rgba(${line.color.r}, ${line.color.g}, ${line.color.b}, ${line.color.a})`
        }
      );
      thunkApi.dispatch(GetLinesFromBase(""))
      const figure: IDrawTriangleItem = {
        id: response.data.id,
        color: line.color,
        type: "line",
        list: [
          {id: 1, clickDown: false, isMove: false, Coordinate: {x: response.data.x1, y: response.data.y1}},
          {id: 2, clickDown: false, isMove: false, Coordinate: {x: response.data.x2, y: response.data.y2}}
        ]
      }
      return figure;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const GetLinesFromBase = createAsyncThunk<IDrawTriangleItem[], string>(
  "draw/getLines",
  async (_, thunkApi) => {
    try {
      const response = await clientApi().get<LinesListApi>("/draw/line", {});
      const list: IDrawTriangleItem[] = response.data.map(obj => {
        const newObjColor = {r: 0, g: 0, b: 0, a: 0}
        if (obj.color != null) {
          const colorArray = obj.color?.split("(")[1].split(")")[0].split(",");
          newObjColor.r = Number(colorArray[0]);
          newObjColor.g = Number(colorArray[1]);
          newObjColor.b = Number(colorArray[2]);
          newObjColor.a = Number(colorArray[3]);
        }
        const figure: IDrawTriangleItem = {
          id: obj.id,
          color: newObjColor,
          type: "line",
          list: [
            {id: 1, clickDown: false, isMove: false, Coordinate: {x: obj.x1, y: obj.y1}},
            {id: 2, clickDown: false, isMove: false, Coordinate: {x: obj.x2, y: obj.y2}}
          ]
        }
        return figure;
      })
      return list;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const DeleteLineApi = createAsyncThunk<IDrawTriangleItem[], number>(
  "draw/delteLine",
  async (id, thunkApi) => {
    try {
      const response = await clientApi().delete<boolean>("/draw/line", {data: {id}});
      let list: IDrawTriangleItem[] = [];
      if (response) {
        const response2 = await clientApi().get<LinesListApi>("/draw/line");
        list = response2.data.map(obj => {
          const newObjColor = {r: 0, g: 0, b: 0, a: 0}
          if (obj.color != null) {
            const colorArray = obj.color?.split("(")[1].split(")")[0].split(",");
            newObjColor.r = Number(colorArray[0]);
            newObjColor.g = Number(colorArray[1]);
            newObjColor.b = Number(colorArray[2]);
            newObjColor.a = Number(colorArray[3]);
          }
          const figure: IDrawTriangleItem = {
            id: obj.id,
            color: newObjColor,
            type: "line",
            list: [
              {id: 1, clickDown: false, isMove: false, Coordinate: {x: obj.x1, y: obj.y1}},
              {id: 2, clickDown: false, isMove: false, Coordinate: {x: obj.x2, y: obj.y2}}
            ]
          }
          return figure;
        });
      } else {
        thunkApi.rejectWithValue("Удаление не удалось")
      }
      return list;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const LineSlice = createSlice({
  name: "Linedraw",
  initialState,
  reducers: {
    setCurrentLine: (state, action: PayloadAction<number>) => {
      state.currentLineItemID = action.payload;
    },
    setTriangleList: (state, action: PayloadAction<IDrawTriangleItem[]>) => {
      state.lineList = action.payload;
    },
    toggleAdd: (state, action: PayloadAction<boolean>) => {
      state.isAdd = action.payload;
    },

    setEdit(state, action: PayloadAction<boolean>) {
      state.isEdit = action.payload;
    },
    resetCurrentId: (state) => {
      state.currentLineItemID = null;
    },
    setColor: (state, action: PayloadAction<{id: number; color: {r: number, g: number, b: number, a: number;}}>) => {
      state.lineList = state.lineList.map((obj) => {
        if (obj.id === action.payload.id) {
          return {...obj, color: action.payload.color}
        } else {
          return obj;
        }
      })
    }
  },
  extraReducers: (builder) => {
    builder.addCase(PutLineToBase.pending, (state) => {
      state.progressLine = "loading";
    
    });
    builder.addCase(PutLineToBase.fulfilled, (state, action: PayloadAction<IDrawTriangleItem>) => {
      state.progressLine = "success"
      state.lineList = state.lineList.map(obj => {
        if (obj.id === action.payload.id) {
          return action.payload;
        } else {
          return obj;
        }
      })
    })
    builder.addCase(PutLineToBase.rejected, (state) => {
      state.progressLine = "error"
    })

    builder.addCase(PostLineToBase.pending, (state) => {
      state.progressLine = "loading"
    })
    builder.addCase(PostLineToBase.fulfilled, (state, action: PayloadAction<IDrawTriangleItem>) => {
      state.progressLine = "success";
      state.currentLineItemID = action.payload.id;
    })
    builder.addCase(PostLineToBase.rejected, (state) => {
      state.progressLine = "error"
    })

    builder.addCase(GetLinesFromBase.pending, (state) => {
      state.progressLine = "loading"
    })
    builder.addCase(GetLinesFromBase.fulfilled, (state, action: PayloadAction<IDrawTriangleItem[]>) => {
      state.progressLine = "success";
      state.lineList = action.payload;
    })
    builder.addCase(GetLinesFromBase.rejected, (state) => {
      state.progressLine = "error"
    })

    builder.addCase(DeleteLineApi.pending, (state) => {
      state.progressLine = "loading"
    })
    builder.addCase(DeleteLineApi.fulfilled, (state, action: PayloadAction<IDrawTriangleItem[]>) => {
      state.progressLine = "success";
      state.lineList = action.payload;
    })
    builder.addCase(DeleteLineApi.rejected, (state) => {
      state.progressLine = "error"
    })
  }
});

// Action creators are generated for each case reducer function
export const { setCurrentLine, setTriangleList, toggleAdd, setEdit, resetCurrentId, setColor } = LineSlice.actions;

export default LineSlice.reducer;
