import { FC, memo } from "react";
import { useAppDispatch } from "../../hooks/hooks";
import { IDrawDotItemComponent } from "../type";

export const DrawDot: FC<IDrawDotItemComponent> = memo(({ dotItem, methods, id, figureID, isAdd, setCurrentItem }) => {
  const dispatch = useAppDispatch();
  return (
    <circle
      style={{cursor: "pointer"}}
      onMouseDown={() => {
        methods.setClickDown(id, true);
        methods.setMove(id, true);
      }}
      onMouseUp={() => {
        methods.setClickDown(id, false);
        methods.setMove(id, false);
        console.log(figureID)
        dispatch(setCurrentItem(figureID))
      }}
      cx={dotItem.Coordinate.x}
      cy={dotItem.Coordinate.y}
      r={isAdd ? "10" : "6"}
      fill="white"
      stroke="black"
    />
  );
});
