import { FC } from "react";
import './InformationGeometry.scss'
import { IDrawTriangleItem } from "../../type";

interface IInformationFigures {
    list: IDrawTriangleItem[];
    typeName: string;
}

export const InformationFigures: FC<IInformationFigures> = ({list, typeName}) => {
    return (
        <div className="draw-block-content__info">
            <h2>Информация из базы данных: </h2>
            <small>Количество фигур: {list.length}</small>
            <ul>
                {list.map(obj => {
                    return (
                    <li key={obj.id} className="figure">
                        <h3>Фигура {typeName}, цвет: {`rgba(${obj.color.r}, ${obj.color.g}, ${obj.color.b}, ${obj.color.a})`}, id: {obj.id}</h3>
                        <p>Количество точек в фигуре: {obj.list.length}</p>
                        <ul>
                            {obj.list.map(dot => {
                                return <li key={dot.id}>Точка: {dot.id}, коордианты: x: {dot.Coordinate.x} и y: {dot.Coordinate.y}</li>
                            })}
                        </ul>
                    </li>)
                })}
            </ul>
        </div>
    )
}