import { FC, useEffect, useState } from "react";
import ColorPicker from "react-pick-color";
import { Button, Message, toaster } from "rsuite";
import io from 'socket.io-client';
import { useAppDispatch, useAppSelector } from "../../hooks/hooks";
import { DrawSvg } from "../DrawComponents/FigureSvg";
import { InformationFigures } from "../DrawComponents/InformationListBase/InformationGeometry";
import { DrawFigureOfType } from "../DrawDotListState";
import { DrawExcel } from "../DrawExcel/DrawExcel";
import {
  DeleteLineApi,
  GetLinesFromBase,
  PostLineToBase,
  PutLineToBase,
  resetCurrentId,
  setColor,
  setCurrentLine,
  setEdit,
  setTriangleList,
} from "./linesFigureSlice";

export const LinesFigure: FC = () => {
  const { isEdit, lineList, currentLineItemID } = useAppSelector(
    (state) => state.draw.lineDraw
  );
  const typeDraw = useAppSelector((state) => state.draw.main.typeDraw);
  const [isPointer, setPointer] = useState(false);
  const dispatch = useAppDispatch();
  const [rgbColor, setRgbColor] = useState({
    b: 255,
    g: 255,
    a: 22,
    r: 255
  })

  const [src, setSrc] = useState("");

  const [isHideBtnAdd, setHideBtnAdd] = useState(false);

  const messageDeleteLineError = (
    <Message showIcon type="error" closable>
      <strong>{"error"}!</strong> Выберите фигуру нажав на точку для удаления
    </Message>
  );

  const messageLimitLineError = (
    <Message showIcon type="error" closable>
      <strong>{"error"}!</strong> Вы превысили лимит линий
    </Message>
  );

  useEffect(() => {
    const socket = io('http://192.168.10.145:5000');
    socket.on('video_frame', function(data) {
        console.log(data)
        setSrc('data:image/jpeg;base64,' + data.frame);
  });
  }, []);

  useEffect(() => {
    dispatch(setTriangleList(lineList));
    setHideBtnAdd(lineList.length >= 10);
  }, [lineList, dispatch]);

  useEffect(() => {
    isHideBtnAdd && toaster.push(messageLimitLineError, {
      placement: "bottomEnd",
      duration: 4000,
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isHideBtnAdd])

  useEffect(() => {
    dispatch(GetLinesFromBase(""));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch]);

  

  useEffect(() => {
    if (isEdit) {
      if (currentLineItemID != null)
        dispatch(
          setColor({
            id: currentLineItemID,
            color: rgbColor,
          })
        );
    }
  }, [currentLineItemID, rgbColor, dispatch, isEdit]);

  useEffect(() => {
    if (isEdit) {
      if (currentLineItemID != null) {
        const color = lineList
          .filter((obj) => obj.id === currentLineItemID)[0].color;
        if (color) {
          setRgbColor(color);
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentLineItemID, isEdit]);

  useEffect(() => {
    dispatch(setEdit(false))
  }, [typeDraw, dispatch]);

  const EditHandler = () => {
    dispatch(setEdit(true));
    setPointer(true);
  }

  const saveEditChangesHandler = () => {
    dispatch(setEdit(false));
    setPointer(false);
    lineList.map((obj) => {
      dispatch(PutLineToBase(obj));
    });
  }

  const deleteFigureHandler = () => {
    dispatch(setEdit(false));
    setPointer(false);
    if (currentLineItemID != null)
      dispatch(DeleteLineApi(currentLineItemID));
    if (currentLineItemID === null)
      toaster.push(messageDeleteLineError, {
        placement: "bottomEnd",
        duration: 4000,
      });
    dispatch(resetCurrentId());
  }

  const addFigureHandler = () => {
    setPointer(false);
    dispatch(
      PostLineToBase({
        id: lineList.length + 1,
        list: DrawFigureOfType(typeDraw),
        color: rgbColor,
        type: typeDraw,
      })
    );
  }

  return (
    <>
       <div className="draw-block-btns">
      
        <Button appearance="ghost" disabled={isEdit} onClick={() => EditHandler()}>
          Редактировать
        </Button>

        {isEdit && (
          <>
            <Button appearance="ghost" color="green" onClick={() => saveEditChangesHandler()}>Сохранить</Button>
            <Button appearance="ghost" color="red" onClick={() => deleteFigureHandler()}>Удалить</Button>
          </>
        )}
        {!isEdit && (
          <Button
            disabled={isHideBtnAdd}
            appearance="ghost"
            className="add"
            onClick={() => addFigureHandler()}
          >
            добавить
          </Button>
        )}
        <DrawExcel list={lineList}/>
      </div>
      <div className="draw-block-content">
        <div className="draw-block-content__video">
            <img src={src} alt="video" />
            {/* {isLoadVideo ? <Loader className="draw-loader" size="lg" content="Загрузка видео" /> : <>
              <DrawSvg triangleList={lineList} isEdit={isPointer} isAdd={isEdit} setCurrentItem={setCurrentLine} />
            </>} */}
            <DrawSvg triangleList={lineList} isEdit={isPointer} isAdd={isEdit} setCurrentItem={setCurrentLine} />
        </div>
        <ColorPicker className="picker" color={rgbColor} onChange={({rgb}) => setRgbColor(rgb)} />
        <InformationFigures list={lineList} typeName="Линия"/>
      </div>
    </>
  );
};
