import { FC } from "react";
import { IDrawTriangleItem } from "../type";
import { CSVLink } from "react-csv";
import FileDownloadIcon from "@rsuite/icons/FileDownload";
import { Tooltip, Whisper } from "rsuite";

interface IDrawExcel {
  list: IDrawTriangleItem[];
}
const tooltip = (
    <Tooltip>
       Скачать файл Excel
    </Tooltip>
  );

export const DrawExcel: FC<IDrawExcel> = ({ list }) => {
  const convertList = (list: IDrawTriangleItem[]) => {
    const newList = list.map((obj) => {
      const newObject = { Line: obj.id, Color: `rgba(${obj.color.r}, ${obj.color.g}, ${obj.color.b}, ${obj.color.a})` };
      obj.list.forEach((item) => {
        Object.assign(newObject, {
          [`dot-${item.id}`]: `x: ${item.Coordinate.x}, y: ${item.Coordinate.y}`,
        });
      });
      return newObject;
    });
    return newList;
  };
  return (
    <Whisper
      placement="top"
      controlId="control-id-hover"
      trigger="hover"
      speaker={tooltip}
    >
      <CSVLink
        className="draw-csv-download"
        data={convertList(list)}
        filename="figure options"
      >
        <FileDownloadIcon width={25} height={25} />
      </CSVLink>
    </Whisper>
  );
};
