import { useEffect, useState } from "react";
import { Coordinate, IUseDrawDot } from "../DrawVideo/type";

export interface IuseDrawDotElement {
  id: number;
  clickDown: boolean;
  isMove: boolean;
  Coordinate: Coordinate;
}
export type useDrawDotList = IuseDrawDotElement[];

export const useDrawDot = (dots: useDrawDotList): IUseDrawDot => {
  const [dotsList, setList] = useState<useDrawDotList>(dots);
  const [pathD, setPathD] = useState("");
  const [currentId, setCurrentId] = useState(1);

  useEffect(() => {
    setList(dots);
  }, [dots])

  useEffect(() => {
    let linesStr = "";
    dotsList.forEach((obj, index) => {
      if (index != dotsList.length - 1) {
        linesStr += `L${obj.Coordinate.x} ${obj.Coordinate.y}`;
      } else {
        linesStr;
      }
    });
    setPathD(
      `M${dotsList[dotsList.length - 1].Coordinate.x} ${
        dotsList[dotsList.length - 1].Coordinate.y
      }${linesStr} L${dotsList[dotsList.length - 1].Coordinate.x} ${
        dotsList[dotsList.length - 1].Coordinate.y
      }Z`
    );
  }, [dotsList]);

  const setClickDown = (id: number, boolean: boolean) => {
    setCurrentId(id);
    setList(
      dotsList.map((obj) =>
        obj.id === id ? { ...obj, clickDown: boolean } : obj
      )
    );
  };

  const setMove = (id: number, boolean: boolean | undefined) => {
    setList(
      dotsList.map((obj) =>
        obj.id === id ? { ...obj, isMove: boolean ? boolean : false } : obj
      )
    );
  };

  const setCoordinate = (id: number, Coordinate: Coordinate) => {
    setCurrentId(id);
    setList(
      dotsList.map((obj) => (obj.id === id ? { ...obj, Coordinate } : obj))
    );
  };

  return {
    state: dotsList,
    methods: { setClickDown, setMove, setCoordinate },
    currentId,
    pathD,
  };
};
