import React, { FC, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Nav, Row } from 'rsuite';
import './Nav.scss';

interface ICustomNav {
    active: string;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    onSelect: (eventKey: any, event: React.SyntheticEvent<Element, Event>) => void
}

const CustomNav: FC<ICustomNav> = ({ active, onSelect, ...props }) => {
  return (
    <Nav {...props} vertical activeKey={active} onSelect={onSelect} style={{ width: 200 }}>
      <Nav.Item eventKey="home">Главная</Nav.Item>
      <Nav.Item eventKey="video">Видео камера</Nav.Item>
    </Nav>
  );
};

export const NavBar: FC = () => {
    const [active, setActive] = React.useState('home');
    const navigate = useNavigate();

    useEffect(() => {
        navigate(active);
    }, [active, navigate])

    return (
      <Row className="navigation">
          <CustomNav active={active} onSelect={setActive} />
      </Row>
    )
}