import { FC } from "react";
import { ValueType } from "rsuite/esm/Checkbox";
import { useAppSelector } from "../hooks/hooks";
import { DrawFilter } from "./DrawFilter/DrawFilter";
import { LinesFigure } from "./LinesFigure/LinesFigure";
import './index.scss';
import { HexagonFigure } from "./HexagonFigure/HexagonFigure";

export const VideoDrawComponent = () => {
    const { typeDraw } = useAppSelector(state => state.draw.main);

    return (
        <>
            <DrawFilter />
            <GetDrawComponent type={typeDraw}/>
        </>
    );
};

interface IGetDrawComponent {
    type: ValueType
}
const GetDrawComponent: FC<IGetDrawComponent> = ({type}) => {
    switch(type) {
        case "line":
            return <LinesFigure />
        case "hexagon":
            return <HexagonFigure />
    }
}
