import { useEffect, useState } from "react";
import { Radio, RadioGroup } from "rsuite";
import { ValueType } from "rsuite/esm/Checkbox";
import { useAppDispatch } from "../../hooks/hooks";
import { setTypeDraw } from "../svgDrawSlice";

export const DrawFilter = () => {
    const [val, setVal] = useState<ValueType>("line");

    const dispatch = useAppDispatch();

    useEffect(() => {
        dispatch(setTypeDraw(val));
    }, [val, dispatch])
    return (
        <RadioGroup name="radio-group-inline" inline defaultValue="line" onChange={(val) => setVal(val)}>
            <Radio value="line">Зона вход-выход</Radio>
            <Radio value="hexagon">Область отслеживания</Radio>
        </RadioGroup>
    );
};
