import { PayloadAction, createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { clientApi } from "../../api/api";
import { DrawTriangleList } from "../DrawDotListState";
import { IDrawTriangleItem, PutHexagonApi } from "../type";

export interface HexagonState {
  hexagonList: IDrawTriangleItem[];
  currentHexagonItemID: number | null;
  isAdd: boolean;
  isEdit: boolean;
  progressHexagon: "success" | "loading" | "error";
}

const initialState: HexagonState = {
  hexagonList: DrawTriangleList,
  currentHexagonItemID: null,
  isAdd: false,
  isEdit: false,
  progressHexagon: "success"
};

export const PutHexagonToBase = createAsyncThunk<IDrawTriangleItem, IDrawTriangleItem>(
  "draw/changeHexagonBase",
  async (line, thunkApi) => {
    try {
      const response = await clientApi().put<PutHexagonApi>(
        "/draw/hexagon",
        {
          id: line.id,
          x1: line.list[0].Coordinate.x,
          y1: line.list[0].Coordinate.y,
          x2: line.list[1].Coordinate.x,
          y2: line.list[1].Coordinate.y,
          x3: line.list[2].Coordinate.x,
          y3: line.list[2].Coordinate.y,
          x4: line.list[3].Coordinate.x,
          y4: line.list[3].Coordinate.y,
          x5: line.list[4].Coordinate.x,
          y5: line.list[4].Coordinate.y,
          x6: line.list[5].Coordinate.x,
          y6: line.list[5].Coordinate.y,
          color: `rgba(${line.color.r}, ${line.color.g}, ${line.color.b}, ${line.color.a})`
        }
      );
      const figure: IDrawTriangleItem = {
        id: response.data.id,
        color: line.color,
        type: "hexagon",
        list: [
          {id: 1, clickDown: false, isMove: false, Coordinate: {x: response.data.x1, y: response.data.y1}},
          {id: 2, clickDown: false, isMove: false, Coordinate: {x: response.data.x2, y: response.data.y2}},
          {id: 3, clickDown: false, isMove: false, Coordinate: {x: response.data.x3, y: response.data.y3}},
          {id: 4, clickDown: false, isMove: false, Coordinate: {x: response.data.x4, y: response.data.y4}},
          {id: 5, clickDown: false, isMove: false, Coordinate: {x: response.data.x5, y: response.data.y5}},
          {id: 6, clickDown: false, isMove: false, Coordinate: {x: response.data.x6, y: response.data.y6}}
        ]
      }
      return figure;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const PostHexagonToBase = createAsyncThunk<IDrawTriangleItem, IDrawTriangleItem>(
  "draw/addHexagonBase",
  async (line, thunkApi) => {
    try {
      const response = await clientApi().post<PutHexagonApi>(
        "/draw/hexagon",
        {
          x1: line.list[0].Coordinate.x,
          y1: line.list[0].Coordinate.y,
          x2: line.list[1].Coordinate.x,
          y2: line.list[1].Coordinate.y,
          x3: line.list[2].Coordinate.x,
          y3: line.list[2].Coordinate.y,
          x4: line.list[3].Coordinate.x,
          y4: line.list[3].Coordinate.y,
          x5: line.list[4].Coordinate.x,
          y5: line.list[4].Coordinate.y,
          x6: line.list[5].Coordinate.x,
          y6: line.list[5].Coordinate.y,
          color: `rgba(${line.color.r}, ${line.color.g}, ${line.color.b}, ${line.color.a})`
        }
      );
      const figure: IDrawTriangleItem = {
        id: response.data.id,
        color: line.color,
        type: "line",
        list: [
          {id: 1, clickDown: false, isMove: false, Coordinate: {x: response.data.x1, y: response.data.y1}},
          {id: 2, clickDown: false, isMove: false, Coordinate: {x: response.data.x2, y: response.data.y2}},
          {id: 3, clickDown: false, isMove: false, Coordinate: {x: response.data.x3, y: response.data.y3}},
          {id: 4, clickDown: false, isMove: false, Coordinate: {x: response.data.x4, y: response.data.y4}},
          {id: 5, clickDown: false, isMove: false, Coordinate: {x: response.data.x5, y: response.data.y5}},
          {id: 6, clickDown: false, isMove: false, Coordinate: {x: response.data.x6, y: response.data.y6}}
        ]
      }
      thunkApi.dispatch(GetHexagonFromBase(""))
      return figure;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const GetHexagonFromBase = createAsyncThunk<IDrawTriangleItem[], string>(
  "draw/getHexagons",
  async (_, thunkApi) => {
    try {
      const response = await clientApi().get<PutHexagonApi[]>("/draw/hexagon");
      const list: IDrawTriangleItem[] = response.data.map(obj => {
        const newObjColor = {r: 0, g: 0, b: 0, a: 0}
        if (obj.color != null) {
          const colorArray = obj.color?.split("(")[1].split(")")[0].split(",");
          newObjColor.r = Number(colorArray[0]);
          newObjColor.g = Number(colorArray[1]);
          newObjColor.b = Number(colorArray[2]);
          newObjColor.a = Number(colorArray[3]);
        }
        const figure: IDrawTriangleItem = {
          id: obj.id,
          color: newObjColor,
          type: "hexagon",
          list: [
            {id: 1, clickDown: false, isMove: false, Coordinate: {x: obj.x1, y: obj.y1}},
            {id: 2, clickDown: false, isMove: false, Coordinate: {x: obj.x2, y: obj.y2}},
            {id: 3, clickDown: false, isMove: false, Coordinate: {x: obj.x3, y: obj.y3}},
            {id: 4, clickDown: false, isMove: false, Coordinate: {x: obj.x4, y: obj.y4}},
            {id: 5, clickDown: false, isMove: false, Coordinate: {x: obj.x5, y: obj.y5}},
            {id: 6, clickDown: false, isMove: false, Coordinate: {x: obj.x6, y: obj.y6}}
          ]
        }
        return figure;
      })
      return list;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const DeleteHexagonApi = createAsyncThunk<IDrawTriangleItem[], number>(
  "draw/deleteHexagon",
  async (id, thunkApi) => {
    try {
      const response = await clientApi().delete<boolean>("/draw/hexagon", {data: {id}});
      let list: IDrawTriangleItem[] = [];
      if (response) {
        const response2 = await clientApi().get<PutHexagonApi[]>("/draw/hexagon");
        list = response2.data.map(obj => {
          const newObjColor = {r: 0, g: 0, b: 0, a: 0}
          if (obj.color != null) {
            const colorArray = obj.color?.split("(")[1].split(")")[0].split(",");
            newObjColor.r = Number(colorArray[0]);
            newObjColor.g = Number(colorArray[1]);
            newObjColor.b = Number(colorArray[2]);
            newObjColor.a = Number(colorArray[3]);
          }
          const figure: IDrawTriangleItem = {
            id: obj.id,
            color: newObjColor,
            type: "hexagon",
            list: [
              {id: 1, clickDown: false, isMove: false, Coordinate: {x: obj.x1, y: obj.y1}},
              {id: 2, clickDown: false, isMove: false, Coordinate: {x: obj.x2, y: obj.y2}},
              {id: 3, clickDown: false, isMove: false, Coordinate: {x: obj.x3, y: obj.y3}},
              {id: 4, clickDown: false, isMove: false, Coordinate: {x: obj.x4, y: obj.y4}},
              {id: 5, clickDown: false, isMove: false, Coordinate: {x: obj.x5, y: obj.y5}},
              {id: 6, clickDown: false, isMove: false, Coordinate: {x: obj.x6, y: obj.y6}}
            ]
          }
          return figure;
        });
      } else {
        thunkApi.rejectWithValue("Удаление не удалось")
      }
      return list;
    } catch (err) {
      return thunkApi.rejectWithValue(
        `Ошибка сервера ${(err as Error).message}`
      );
    }
  }
);

export const HexagonSlice = createSlice({
  name: "Linedraw",
  initialState,
  reducers: {
    setCurrentLine: (state, action: PayloadAction<number>) => {
      state.currentHexagonItemID = action.payload;
    },
    setTriangleList: (state, action: PayloadAction<IDrawTriangleItem[]>) => {
      state.hexagonList = action.payload;
    },
    toggleAdd: (state, action: PayloadAction<boolean>) => {
      state.isAdd = action.payload;
    },

    setEdit(state, action: PayloadAction<boolean>) {
      state.isEdit = action.payload;
    },
    resetCurrentId: (state) => {
      state.currentHexagonItemID = null;
    },
    setColor: (state, action: PayloadAction<{id: number; color: {r: number, g: number, b: number, a: number}}>) => {
      state.hexagonList = state.hexagonList.map((obj) => {
        if (obj.id === action.payload.id) {
          return {...obj, color: action.payload.color}
        } else {
          return obj;
        }
      })
    }
  },
  extraReducers: (builder) => {
    builder.addCase(PutHexagonToBase.pending, (state) => {
      state.progressHexagon = "loading";
    
    });
    builder.addCase(PutHexagonToBase.fulfilled, (state, action: PayloadAction<IDrawTriangleItem>) => {
      state.progressHexagon = "success"
      state.hexagonList = state.hexagonList.map(obj => {
        if (obj.id === action.payload.id) {
          return action.payload;
        } else {
          return obj;
        }
      })
    })
    builder.addCase(PutHexagonToBase.rejected, (state) => {
      state.progressHexagon = "error"
    })

    builder.addCase(PostHexagonToBase.pending, (state) => {
      state.progressHexagon = "loading"
    })
    builder.addCase(PostHexagonToBase.fulfilled, (state, action: PayloadAction<IDrawTriangleItem>) => {
      state.progressHexagon = "success";
      state.currentHexagonItemID = action.payload.id;
    })
    builder.addCase(PostHexagonToBase.rejected, (state) => {
      state.progressHexagon = "error"
    })

    builder.addCase(GetHexagonFromBase.pending, (state) => {
      state.progressHexagon = "loading"
    })
    builder.addCase(GetHexagonFromBase.fulfilled, (state, action: PayloadAction<IDrawTriangleItem[]>) => {
      state.progressHexagon = "success";
      state.hexagonList = action.payload;
    })
    builder.addCase(GetHexagonFromBase.rejected, (state) => {
      state.progressHexagon = "error"
    })

    builder.addCase(DeleteHexagonApi.pending, (state) => {
      state.progressHexagon = "loading"
    })
    builder.addCase(DeleteHexagonApi.fulfilled, (state, action: PayloadAction<IDrawTriangleItem[]>) => {
      state.progressHexagon = "success";
      state.hexagonList = action.payload;
    })
    builder.addCase(DeleteHexagonApi.rejected, (state) => {
      state.progressHexagon = "error"
    })
  }
});

// Action creators are generated for each case reducer function
export const { setCurrentLine, setTriangleList, toggleAdd, setEdit, resetCurrentId, setColor } = HexagonSlice.actions;

export default HexagonSlice.reducer;
