import { FC, useEffect, useState } from "react";

import ColorPicker, { RgbColor } from "react-pick-color";
import { Button, Message, toaster } from "rsuite";
import io from 'socket.io-client';
import { useAppDispatch, useAppSelector } from "../../hooks/hooks";
import { DrawSvg } from "../DrawComponents/FigureSvg";
import { InformationFigures } from "../DrawComponents/InformationListBase/InformationGeometry";
import { DrawFigureOfType } from "../DrawDotListState";
import { DrawExcel } from "../DrawExcel/DrawExcel";
import {
  DeleteHexagonApi,
  GetHexagonFromBase,
  PostHexagonToBase,
  PutHexagonToBase,
  resetCurrentId,
  setColor,
  setCurrentLine,
  setEdit,
  setTriangleList,
} from "./hexagonFigureSlice";

export const HexagonFigure: FC = () => {
  const { isEdit, hexagonList, currentHexagonItemID } = useAppSelector(
    (state) => state.draw.hexagonDraw
  );
  const typeDraw = useAppSelector((state) => state.draw.main.typeDraw);
  const [isPointer, setPointer] = useState(false);
  const [rgbColor, setRgbColor] = useState<RgbColor>({
    b: 255,
    g: 255,
    a: 255,
    r: 22,
  });
  const dispatch = useAppDispatch();

  const [src, setSrc] = useState("");

  const messageDeleteLineError = (
    <Message showIcon type="error" closable>
      <strong>{"error"}!</strong> Выберите фигуру нажав на точку для удаления
    </Message>
  );

  useEffect(() => {
    dispatch(setTriangleList(hexagonList));
  }, [hexagonList, dispatch]);

  useEffect(() => {
    dispatch(GetHexagonFromBase(""));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch]);


  useEffect(() => {
    if (isEdit) {
      if (currentHexagonItemID != null)
        dispatch(
          setColor({
            id: currentHexagonItemID,
            color: rgbColor,
          })
        );
    }
  }, [currentHexagonItemID, rgbColor, dispatch, isEdit]);

  useEffect(() => {
    if (isEdit) {
      if (currentHexagonItemID != null) {
        const color = hexagonList.filter(
          (obj) => obj.id === currentHexagonItemID
        )[0].color;
        if (color) {
          setRgbColor(color);
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentHexagonItemID, isEdit]);

  useEffect(() => {
    dispatch(setEdit(false));
  }, [typeDraw, dispatch]);

  useEffect(() => {
    const socket = io('http://192.168.10.145:5000');
    socket.on('video_frame', function(data) {
      setSrc('data:image/jpeg;base64,' + data.frame);
});
  }, [])

  const EditHandler = () => {
    dispatch(setEdit(true));
    setPointer(true);
  };

  const saveEditChangesHandler = () => {
    dispatch(setEdit(false));
    setPointer(false);
    hexagonList.map((obj) => {
      dispatch(PutHexagonToBase(obj));
    });
  };

  const deleteFigureHandler = () => {
    dispatch(setEdit(false));
    setPointer(false);
    if (currentHexagonItemID != null)
      dispatch(DeleteHexagonApi(currentHexagonItemID));
    if (currentHexagonItemID === null)
      toaster.push(messageDeleteLineError, {
        placement: "bottomEnd",
        duration: 4000,
      });
    dispatch(resetCurrentId());
  };

  const addFigureHandler = () => {
    dispatch(
      PostHexagonToBase({
        id: hexagonList.length + 1,
        list: DrawFigureOfType(typeDraw),
        color: rgbColor,
        type: typeDraw,
      })
    );
  };

  return (
    <>
      <div className="draw-block-btns">
        <Button
          appearance="ghost"
          disabled={isEdit}
          onClick={() => EditHandler()}
        >
          Редактировать
        </Button>

        {isEdit && (
          <>
            <Button
              appearance="ghost"
              color="green"
              onClick={() => saveEditChangesHandler()}
            >
              Сохранить
            </Button>
            <Button
              appearance="ghost"
              color="red"
              onClick={() => deleteFigureHandler()}
            >
              Удалить
            </Button>
          </>
        )}
        {!isEdit && (
          <Button
            appearance="ghost"
            className="add"
            onClick={() => addFigureHandler()}
          >
            добавить
          </Button>
        )}
        <DrawExcel list={hexagonList} />
      </div>
      <div className="draw-block-content">
        <div className="draw-block-content__video">
          <img src={src} alt="Video" />
          <DrawSvg
            triangleList={hexagonList}
            isEdit={isPointer}
            isAdd={isEdit}
            setCurrentItem={setCurrentLine}
          />
        </div>
        <ColorPicker
          className="picker"
          color={rgbColor}
          onChange={({ rgb }) => setRgbColor(rgb)}
        />
        <InformationFigures list={hexagonList} typeName="Шестиугольник" />
      </div>
    </>
  );
};
