import React, { FC, useEffect, useRef, useState } from "react";
import { Figure } from "./FigureItem";
import { Coordinate, IDrawTriangleItem } from "../type";
import { ActionCreatorWithPayload } from "@reduxjs/toolkit";

interface IDrawSvg {
  triangleList: IDrawTriangleItem[]
  isEdit: boolean;
  isAdd: boolean;
  setCurrentItem: ActionCreatorWithPayload<number, string>
}

export const DrawSvg: FC<IDrawSvg> = ({triangleList, isEdit, isAdd, setCurrentItem}) => {
  const svgRef = useRef<SVGSVGElement | null>(null);
  const [isMouseEnter, setMouseEnter] = useState(false);
  const [globalCoordinate, setGlobalCoordinate] = useState<Coordinate>();

  const svgMouseEnter = () => {
    setMouseEnter(true);
   
  };
  const svgMouseLeave = () => {
    setMouseEnter(false);
  };
  const svgMouseMove = (e: React.MouseEvent<SVGSVGElement, MouseEvent>) => {
    if (svgRef.current) {
      const rect = svgRef.current.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;
      setGlobalCoordinate({x, y});
    }
  };

  useEffect(() => {
    console.log(triangleList)
  }, [triangleList])

  return (
    <svg
      width="100%"
      height="100%"
      viewBox="0 0 100% 100%"
      fill="none"
      style={{
        position: "absolute",
        inset: 0,
        height: "100%",
        width: "100%",
        zIndex: 2,
        pointerEvents: isEdit ? "auto" : "none",
      }}
      xmlns="http://www.w3.org/2000/svg"
      ref={svgRef}
      onMouseEnter={svgMouseEnter}
      onMouseLeave={svgMouseLeave}
      onMouseMove={svgMouseMove}
    >
      {triangleList && triangleList.map(item => {
        return <Figure isAdd={isAdd} setCurrentItem={setCurrentItem} triangleList={triangleList} key={item.id} item={item} dots={item.list} isMouseEnter={isMouseEnter} coordinate={globalCoordinate} />
      })}
    </svg>
  );
};
