import { ValueType } from "rsuite/esm/Checkbox";
import { IDrawTriangleItem } from "./type";
import { useDrawDotList } from "../hooks/useDrawDot";
// import { nanoid } from "@reduxjs/toolkit";

export function random(a: number, b: number) {
    return Math.floor(Math.random() * Math.abs(a - b + 1)) + Math.min(a, b);
}

// const DotItem = { id: nanoid(), clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } };

export const DotsList: useDrawDotList = [
  { id: 1, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
  { id: 2, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
];

export const DrawTriangleList: IDrawTriangleItem[] = [
    {
        id: 1,
        list: DotsList,
        color: {
            r: random(0, 255),
            g: random(0, 255),
            b: random(0, 255),
            a: random(0, 255)
        },
        type: "line"
    }
];

export const DrawFigureOfType = (type: ValueType): useDrawDotList => {
    let array: useDrawDotList = [];
    switch (type) {
        case "line":
            array = [
              { id: 1, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
              { id: 2, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } }
            ];
            break;
        case "hexagon":
            array = [
              { id: 1, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
              { id: 2, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
              { id: 3, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
              { id: 4, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
              { id: 5, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
              { id: 6, clickDown: false, isMove: false, Coordinate: { x: random(0, 600), y: random(0, 450) } },
            ];
            break;
    }
    return array;
};
