import { ActionCreatorWithPayload } from "@reduxjs/toolkit";
import { FC, memo, useEffect } from "react";
import { useAppDispatch } from "../../hooks/hooks";
import { useDrawDot, useDrawDotList } from "../../hooks/useDrawDot";
import { setTriangleList } from "../LinesFigure/linesFigureSlice";
import { Coordinate, IDrawTriangleItem } from "../type";
import { DrawDot } from "./DrawDot";




interface IFigure {
  isMouseEnter: boolean;
  coordinate: Coordinate | undefined;
  dots: useDrawDotList;
  triangleList?: IDrawTriangleItem[];
  item: IDrawTriangleItem;
  isAdd: boolean;
  setCurrentItem: ActionCreatorWithPayload<number, string>
}

const FigureItem: FC<IFigure> = ({
  isMouseEnter,
  coordinate,
  dots,
  triangleList,
  item,
  isAdd,
  setCurrentItem
}) => {
  const { state, methods, currentId, pathD } = useDrawDot(dots);
  const dispatch = useAppDispatch();
  useEffect(() => {
    const newList = triangleList?.map(obj => {
      if (obj.id === item.id) {
        return {...obj, list: state}
      } else {
        return obj;
      }
    })
    dispatch(setTriangleList(newList ? newList : []))
  }, [state, item.id, triangleList, dispatch]);

  useEffect(() => {
    if (isMouseEnter) {
      methods.setMove(
        currentId,
        state.find((obj) => obj.id === currentId)?.clickDown
      );
    } else {
      methods.setMove(currentId, false);
      methods.setClickDown(currentId, false);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isMouseEnter]);

  useEffect(() => {
    if (coordinate) {
      state.find((obj) => obj.id === currentId)?.isMove &&
        methods.setCoordinate(currentId, coordinate);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [coordinate]);

  return (
    <g className={`g-${item.id}`}>
      <path strokeWidth={5} d={pathD} fill={`rgba(${item.color.r}, ${item.color.g}, ${item.color.b}, ${item.color.a})`} stroke={`rgba(${item.color.r}, ${item.color.g}, ${item.color.b}, ${item.color.a})`} />
      {state.map((obj) => {
        return (
          <DrawDot isAdd={isAdd} setCurrentItem={setCurrentItem} figureID={item.id} key={obj.id} dotItem={obj} methods={methods} id={obj.id} />
        );
      })}
    </g>
  );
};


export const Figure = memo(FigureItem);