import { PayloadAction, combineReducers, createSlice } from "@reduxjs/toolkit";
import { ValueType } from "rsuite/esm/Checkbox";
import lineDraw from './LinesFigure/linesFigureSlice'
import hexagonDraw from './HexagonFigure/hexagonFigureSlice';

export interface SvgDrawState {
  typeDraw: ValueType;
}

const initialState: SvgDrawState = {
  typeDraw: "line"
};

export const SvgDrawSlice = createSlice({
  name: "svgdraw",
  initialState,
  reducers: {
    setTypeDraw: (state, action: PayloadAction<ValueType>) => {
      state.typeDraw = action.payload;
    }
  }
});

// Action creators are generated for each case reducer function
export const { setTypeDraw} = SvgDrawSlice.actions;

export default combineReducers({main: SvgDrawSlice.reducer, lineDraw, hexagonDraw});
